import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  Button,
  Group,
  Link,
  Avatar,
  isVIP
} from "@vkontakte/vkui";
import {
  LineChart,
  XAxis,
  YAxis,
  Tooltip,
  CartesianGrid,
  Line,
  Label,
  Legend,
  AreaChart,
  Area,
} from "recharts";

import { IntuitionItem, ModalIntuitionResult } from "../gamesItem";
import { ModalIntuition } from "../modal";
import {
  formatCoins,
  SocketManager,
  dataGames,
  formatCoinsToServer,
  delay,
} from "../helpers";

const { callMethod } = SocketManager;

let interval = null;
let events = false;

const DurkaCoinCrash = ({
  id,
  go,
  connectTwoDevices,
  stub,
  socketReconnect,
  networkStatus,
  updateConnection,
  socketReconnection,
  snackbar,
  durkaCoins,
  openBuyDurkaCoins,
  setActiveModal,
  openAlertPay,
  openBase,
  openMenuVIP,
  getPlatform,
}) => {

  let number = "";
  let numberResult = 10;
  let globalBank = 0;
  let min = 0;
  let max = 300;
  let num = 0;
  let game = true;
  // let numberItem = 0;
  const [dataBet, setDataBet] = useState([]);
  const [bank, setBank] = useState(0);
  const [screenBank, setScreenBank] = useState("0");
  const [timer, setTimer] = useState(300);
  const [screenTimer, setScreenTimer] = useState("00:00");
  const [numberItem, setNumberItem] = useState(0);
  const [randomNumber, setRandomNumber] = useState(0);
  const [winners, setWinners] = useState([]);
  const [renderItemResult, setRenderItemResult] = useState([]);
  const [stateGame, setStateGame] = useState(true);

  const resultRandomNumber = 6.40;
  const [dataChart, setDataChart] = useState([
    //   {
    //   name: '0s', y: 0, x: 2,
    // },
    // {
    //   name: '3s', y: 0, x: 2,
    // },
    // {
    //   name: '6s', y: 0, x: 2,
    // },
    // {
    //   name: '9s', y: 0, x: 2,
    // },
    // {
    //   name: '12s', y: 0, x: 2,
    // },
  ]);

  // const data = [

  //   {
  //     name: '3s', y: 0.25, x: 2,
  //   },
  //   {
  //     name: '6s', y: 1, x: 2,
  //   },
  //   {
  //     name: '10s', y: 3, x: 2,
  //   },

  // ];

  const data = [0, 0.025, 0.05, 0.075, 0.1];
  let width = 0;
  const [widthMobile, setWidthMobile] = useState(0);

  useEffect(() => {
    width = window.innerWidth > 0 ? window.innerWidth : window.screen.width;
    setWidthMobile(Math.round(width * 0.92));

  }, []);


  function generateArr(min, max) {
    let n = 0.10;
    for (let index = 0; index < max; index++) {
      // const rand = Math.random() * (max - min) + min;
      // data.push(rand.toFixed(2));

      n += 0.10;
      data.push(n);
    }

  }


  useEffect(() => {
    generateArr(0, 5);

    console.log("data", data);
    let index = 0;
    let time = 0;
    const interval = setInterval(() => {



      if (index === data.length) {
        clearInterval(interval);
      } else {
        // index = index === 0 ? 0 : time + 3;

        time = index === 0 ? 0 : time + 3;

        // console.log("data y index:", index);


        setDataChart(prev => [...prev,
        {
          ...prev[index],
          time: `${time}s`,
          y: data[index],
          x: data[index]
        }]);
      }
      index++;
      // console.log("dataChart:", dataChart);
    }, 1000);



  }, []);


  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple
        left={
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              flexDirection: "row",
              justifyContent: "center",
            }}
          >
            <PanelHeaderBack
              onClick={(e) => {
                go(e);

                callMethod("enterIntuition", {
                  cancel: true,
                });
                SocketManager.socket.removeEventListener("betsList2");
                SocketManager.socket.removeEventListener("summarize2");
              }}
              data-to="games"
            />


            <Button
              style={{
                backgroundColor: "green",
                marginLeft: "10px",
              }}
              mode="commerce"
              size="m"
              onClick={openBuyDurkaCoins}
            >
              Пополнить
            </Button>
            <Button
              style={{
                backgroundColor: "green",
                marginLeft: "10px",
              }}
              mode="commerce"
              size="m"
            // onClick={openBuyDurkaCoins}
            >
              {/* 00:{seconds} сек */}
              {screenTimer}
            </Button>
          </Div>
        }
      ></PanelHeaderSimple>

      {connectTwoDevices ? (
        stub(
          "Вы подключились с 2 устройств ",
          socketReconnect,
          "Переподключиться"
        )
      ) : (
          <>
            {networkStatus ? (
              stub(
                "Отсутствует интернет - соединение!",
                updateConnection,
                "Повторить"
              )
            ) : (
                <>
                  {socketReconnection ? (
                    stub(
                      "Ошибка подключения к серверу",
                      socketReconnect,
                      "Повторить"
                    )
                  ) : (
                      <Div
                        style={{
                          padding: 20,
                        }}
                      >

                        <AreaChart
                          width={
                            getPlatform === "web" ? 580 : widthMobile
                          }
                          height={getPlatform === "web" ? 350 : 220}
                          data={dataChart}
                          margin={{
                            top: 10, right: 30, left: 0, bottom: 0,
                          }}
                        >
                          <CartesianGrid strokeDasharray="3 3" />
                          <XAxis dataKey="time" />
                          <YAxis />
                          <Tooltip />
                          <Area type="monotone" dataKey="y" stroke="#8884d8" fill="#00635D" />
                        </AreaChart>

                        {/* <Div>{renderIntuition ? <>{renderIntuition}</> : ""}</Div> */}




                      </Div>
                    )}

                  {snackbar}
                </>
              )}
          </>
        )}
    </Panel>
  );
};

export { DurkaCoinCrash };
